WelcomeScreen {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background: white;
    }
    .login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
    }
    .google-btn {
    position:relative;
    width: 220px;
    height: 50px;
    margin: 1rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
    }
    .google-btn:hover {
    box-shadow: 0 0 15px #4285f4;
    }
    .google-btn:active {
        background: #1669f2;
        }
        .google-icon-wrapper {
        position: absolute;
        margin-top: 2px;
        margin-left: 1px;
        width: 45px;
        height: 45px;
        border-radius: 5px;
        background-color: #fff;
        }
        .google-icon {
        position: absolute;
        text-align: center;
        margin-top: 11px;
        margin-left: -12px;
        width: 25px;
        height: 25px;
        }
        .btn-text {
            position:relative;
            margin-left: 25%;
            background:none;
            border:none;
            margin-top:7%;
            margin-bottom:2%;
            font-size:16px;
            color: white;
            text-align: left;
            font-family: 'Roboto', sans-serif;
        }